.money-box{
    padding-top: 96px;
    padding-bottom: 96px;
    @include box;


    &__title{
      font-family: 'DM Serif Display';
      font-style: normal;
      font-weight: 400;
      font-size: 48px;
      line-height: 56px;
      letter-spacing: -0.369231px;
      color: #36536B;
    }

    .form {
      position: relative;

      &__email {
        width: 100%;
        max-width: 445px;
        height: 48px;
        border: none;
        padding-left: 20px;
        padding-right: 225px;
        box-shadow: 10px 10px 25px -10px 
        rgba(54, 83, 107, 0.25);
        border-radius: 24px;

      }

      &__text {
        padding-left: 20px;
        font-weight: 400;
        font-size: 15px;
        line-height: 18px;
        color: #6C8294;
      }

      &__btn {
        position: absolute;
        right: 0;
        top: 0;
        @include button;
      }
    }

} 

@media only screen and (max-width:771px) {
  .money-box{
   padding-top: 72px;
   padding-bottom: 100px;
   flex-direction: column;

   &__title{
     padding-bottom: 40px;
   }

  }
}

@media only screen and (max-width:480px) {
  .money-box{
         padding-top: 32px;
         padding-bottom: 150px;

       &__title{
         font-size: 32px;
         line-height: 36px;
         text-align: center;
         letter-spacing: -0.246154px;
       }

        .form{
     &__btn{
       width: 327px;
       top: 65px;
       left: 2px;
     }

     &__email{
       width: 327px;
       margin: 0;
     }

     &__text{
       position: absolute;
       top: 140px;
       padding-left: 50px;
     }
   }
  }
}